@font-face {
  font-family: "Montserrat";
  src: local("Montserrat"), local("Montserrat-Regular"), url("/fonts/montserrat.woff2") format("woff2"),
    url("/fonts/montserrat.woff") format("woff"), url("/fonts/montserrat.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: local("Montserrat Bold"), local("Montserrat-Bold"), url("/fonts/montserratbold.woff2") format("woff2"),
    url("/fonts/montserratbold.woff") format("woff"), url("/fonts/montserratbold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: local("Montserrat Black"), local("Montserrat-Black"), url("/fonts/montserratblack.woff2") format("woff2"),
    url("/fonts/montserratblack.woff") format("woff"), url("/fonts/montserratblack.ttf") format("truetype");
  font-weight: 900;
  font-style: normal;
}

* {
  box-sizing: border-box;
}

:root {
  --text-black: rgb(37, 39, 43);
}

body {
  margin: 0;
  color: var(--text-black);
  font-family: Montserrat, Arial, sans-serif;
}

.app {
  padding: 50px;
  border: 1px solid #eee;
  border-radius: 10px;
  margin: 30px auto;
  max-width: 1000px;
  box-shadow: 0 10px 30px #eee;
  display: flex;
  align-items: stretch;
  position: relative;
}

.app__constructor {
  min-width: 300px;
  width: 340px;
  flex-basis: 33%;
  margin-right: 4em;
}

.app__preview {
  flex-basis: 66%;
  border-left: 1px solid #eee;
  padding-left: 2em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.form-group {
  width: 100%;
  margin-bottom: 3em;
}

.checkbox-group {
  margin-bottom: 1em;
}

.checkbox-group > input {
  visibility: hidden;
  display: none;
}

.checkbox-group > label {
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  position: relative;
  font-size: 0.9em;
}

.checkbox-group > label::before {
  cursor: pointer;
  display: block;
  content: "";
  width: 40px;
  height: 20px;
  margin-right: 10px;
  background-color: #ccc;
  border-radius: 10px;
  transition: all 0.2s ease-in-out;
  position: relative;
}

.checkbox-group > label::after {
  display: block;
  content: "";
  width: 22px;
  height: 22px;
  background-color: #fff;
  border-radius: 100px;
  position: absolute;
  box-shadow: 0 0 3px rgb(179, 181, 184);
  transition: all 0.2s cubic-bezier(0.55, -0.58, 0.31, 1.58);
}

.checkbox-group > input:checked ~ label::after {
  transform: translateX(20px);
}

.checkbox-group > input:checked ~ label::before {
  background-color: #40cc39;
}

.input-group {
  margin: 1.5em 0;
  position: relative;
}

.input-group > label {
  font-size: 0.8em;
  line-height: 1;
  position: absolute;
  padding: 0.2em 0.5em;
  top: -0.7em;
  background-color: #fff;
  color: #4370eb;
  border-radius: 4px;
  left: 1em;
  transition: all 0.2s ease-in-out;
}

.input-group > input {
  width: 100%;
  padding: 0.6em 0.9em;
  font-family: Montserrat, Arial, sans-serif;
  font-size: 1.05em;
  border: 1px solid rgb(67, 112, 235);
  border-radius: 5px;
  color: var(--text-black);
}

.input-group > input:focus {
  border: 1px solid #4370eb;
  outline: none;
  box-shadow: 0 0 5px rgba(85, 141, 214, 0.7);
}

.input-group > input:focus ~ label,
.input-group > input:not([value=""]) ~ label {
  font-size: 0.8em;
}

.input-group > input:disabled {
  border-color: #ccc;
  background-color: #fff;
  color: rgb(156, 156, 156);
}

.input-group > input:disabled ~ label {
  color: #aaa;
}

.colors {
  margin: 1em 0 2.5em 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
}

.colors-item {
  width: 25px;
  height: 25px;
  border-radius: 100px;
  margin-right: 10px;
  cursor: pointer;
}

.colors-item {
  width: 24px;
  height: 24px;
  border-radius: 100%;
  margin-right: 10px;
  cursor: pointer;
  box-shadow: 0 1px 3px #aaa;
  transition: all 0.2s cubic-bezier(0.55, -0.58, 0.31, 1.58);
}

.colors-item:hover {
  filter: brightness(0.9) contrast(1.4);
}

.colors-item--active {
  transform: scale(1.3);
  box-shadow: 0 2px 5px rgba(107, 112, 116, 0.7);
}

.buy-wrap {
  display: flex;
  align-items: center;
}

.price {
  margin-left: 1em;
}

.btn {
  font-family: Montserrat, Arial, sans-serif;
  color: #fff;
  border: none;
  background-color: #696c6d;
  border-radius: 4px;
  padding: 0.5em 1.5em;
  transition: all 0.1s ease-in;
  cursor: pointer;
}

.btn-main {
  font-size: 1.1em;
  background-color: #2ba525;
}

.btn-main:hover {
  background-color: #218c1b;
}

.btn:disabled {
  background-color: #ccc;
}

.btn:active {
  transform: translateY(2px);
}

.btn:focus {
  outline: none;
}

.price-text {
  font-size: 0.8em;
  color: #aaa;
}

.price-summ {
  font-size: 1.3em;
  font-weight: 600;
}

.app-title {
  font-size: 1.5em;
  font-weight: 600;
  margin-bottom: 1em;
}

.app-lead {
  font-size: 0.95em;
  line-height: 1.4;
  margin-bottom: 2em;
}

.sign {
  transition: all 0.2s ease-in-out;
}

.sign__disabled {
  opacity: 0.3;
}

svg {
  filter: drop-shadow(0px 3px 5px rgba(168, 168, 168, 0.5));
}

